@font-face {
  font-family: 'MillerBanner';
  font-style: normal;
  font-weight: 400;
  src:
    local('MillerBanner'),
    url(../fonts/MillerBanner.otf) format('otf'),
}

a {
 color: inherit;
 text-decoration: none;
}

a:hover {
    color: #999;
}

.fontMiller {
  font-family: 'MillerBanner', sans-serif;
}

.infoFont {
  font-size: 13px;
}

.headerTitle {
  font-family: 'MillerBanner', sans-serif;
  color: black;
}

.listHeight {
  padding-top: 1.5vh;
  width: 15vw;
  height: 10vh;
  vertical-align: top;
}

.listRight {
  width: 25vw;
}



.paddingMiddle {
  margin-top: 0.9vh;
  margin-bottom: 0.9vh;
}

.adressSize {
  font-size: 11px;
  font-weight: lighter;
}

.ratingText {
  font-size: 9px;
  padding-left: 1vh;
  color: #BBBBBB;
}

.arrowSize {
  margin-top: 0.9vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  font-size: 11px;
  vertical-align: middle;
  color: #BBBBBB;
}

.listBody {
  width: 60%;
  height: 9.5vh;
}

.arrow {
  font-size: 12px;
  vertical-align: bottom;
}

.filterListPadding {
  padding-top: 1.5vh;
}

.filterMenu {
  cursor: pointer;
  padding: 0.5vh 3.8vw 0.5vh 6.5vw;
  font-size: 13px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.menuPadding {
  padding-bottom: 2vh;
}

.listMenu {
  cursor: pointer;
  padding: 0 2vw 0 6.5vw;
  margin-bottom: 3.5vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.hrLight {
  margin: 0vh 4vw 1vw 7vw;
  width: 89vw;
  height: 1px; 
  background-color: #E8E8E8;
  border: 0;
}

hr {
  background-color: #D4B96A;
  height: 1px;
  border: 0;
}




